<template>
	<div class="chinaMapWrapper">
		<van-swipe @change="onChange" :loop="false" :show-indicators="false" ref="mapChild">
		  <van-swipe-item><div class="chart1" ref="myEchart"></div></van-swipe-item>
		  <van-swipe-item><div class="chart1" ref="myEchart2"></div></van-swipe-item>
		</van-swipe>
	</div>
</template>

<script>
	import '../../../node_modules/echarts/map/js/china.js'
	export default{
		data(){
			return{
				nowAreaData:[],//()
				totalAreaData:[]//()
			}
		},
		props:{
			areaData:Array,
			required:true,
		},
		methods:{
			onChange(index){
				//console.log(index)
				this.$emit('innerIndexC',index)
			},
			getNowData(){
				let nowData=[];
				let totalData=[];
				//console.log(this.areaData)
				this.areaData.forEach(item=>{
					if(item.currentConfirmedCount>10000){
						nowData.push({
							name:item.provinceShortName,
							value:item.currentConfirmedCount,
							label:{color:'#fff'}
						})
						totalData.push({
							name:item.provinceShortName,
							value:item.confirmedCount,
							label:{color:"#fff"},
						})
					}
					else{
					nowData.push({
						name:item.provinceShortName,
						value:item.currentConfirmedCount
					})
					totalData.push({
						name:item.provinceShortName,
						value:item.confirmedCount
					})
					}
				})
				this.nowAreaData = nowData;
				this.totalAreaData = totalData;
			},
			//
			getChinaMap(data,ele,kind){//
				let myChart = this.$echarts.init(ele);
				window.onresize = myChart.resize;
				myChart.setOption({
					backgroundColor:'#F7F7F7',//
					title:{
						/* text:'China',
						subTitle:'china',
						color:'#000', */
						x:'center'
					},
					 //
					visualMap: {
						pieces:[
							{value:0,color:'#fff'},
							{gte:1,lte:9,color:'#FAEBD2'},
							{gte:10,lte:99,color:'#E9A188'},
							{gte:100,lte:499,color:'#D56355'},
							{gte:500,lte:999,color:'#BB3937'},
							{gte:1000,lte:10000,color:'#772526'},
							{gt:10000,color:'#480F10'}
						],
						orient:'horizontal',//
						calculable: false, //
						inverse:false,
						textGap:5,
						hoverLink:false,
						itemGap:5,
						itemWidth:10,//
						itemHeight:10,
						left:'center',
						right:'center',
						//show:false,//
						textStyle: {
							color: '#000',
							fontSize:'8',
							fontFamily:'Microsoft YaHei',
							lineHeight:'50',
							textAlign:'center'
						},
					},
					 // 
					tooltip:{
						triggerOn: 'click',
						enterable:true,//
						show:true, //
						trigger: 'item', //
						formatter:function(params){
							//console.log(params)
							var html = `
										<div class="tooltip">
											<div class="tooltip-left">
												<div>:${params.name}</div>
												<p>${kind}:${params.value}</p>
											</div>
											<div class="tooltip-right"><a href="/provinceMap?province=${params.name}">></a></div>
										</div>			
										`
							return html
						},
						textStyle:{
							lineHeight:14,
						}
					},
					series:[
						{
							name:'',
							type: 'map',  //
							map: 'china', //
							showLegendSymbol: true, // legend
							data:data,
							itemStyle: { // 
								fontSize:8,
								emphasis:{ //
									label:{
										show:true,
									}
								}
							},
							zoom:1.1,//
							label: {  //
								show:true,
								fontSize:7,
								fontFamily:'',
								color:'#000',
								fontWeight:'lighter',
							},
						},
						{
							type:'scatter',
							showEffectOn: 'render',//
							coordinateSystem:'geo',//
							symbolSize:10,//
							data:[
								{name: '', value: [111.3,30.7,130]},
							],
							zlevel:99999
						},
					],
				}),
				window.addEventListener('resize', () => {
					// echarts
					myChart.resize();
				})
			}
		},
		created(){
			this.getNowData()
		},
		mounted(){
			this.getChinaMap(this.nowAreaData,this.$refs.myEchart,"");
			this.getChinaMap(this.totalAreaData,this.$refs.myEchart2,"")
		}
	}
</script>

<style scoped="scoped">
	.chinaMapWrapper{
		width: 100%;
		height: 8.43rem;
	}
	.van-swipe{
		width: 100%;
		height: 100%;
	}
	.van-swipe-item{
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.chart1{
		width: 100%;
		min-width: 10rem;
		height: 8.43rem;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.tooltip{
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		font-size: 0.22rem;
		line-height: 14px;
	}
	.tooltip-right{
		display: flex;
		align-items: center;
		flex-direction: row;
		justify-content: center;
		margin-left: 5px;
		font-size: 0.22rem;
	}
	a{
		text-decoration: none;
		color:#fff;
		display: inline-block;
		padding: 0 0.15rem;
		font-size: 0.22rem;
		border-left: 1px solid #fff;
	}
</style>
