<template>
	<div class="chinaMapWrapper">
		<van-swipe @change="onChange" :loop="false" :show-indicators="false" ref="wordlMap1">
		  <van-swipe-item><div class="chart1" ref="wordlMap"></div></van-swipe-item>
		  <van-swipe-item><div class="chart1" ref="worldMap2"></div></van-swipe-item>
		</van-swipe>
	</div>
</template>

<script>
	import '../../../node_modules/echarts/map/js/world.js'
	export default{
		data(){
			return{
				nowAreaData:[],//()
				totalAreaData:[],//()
				defineName:[]
			}
		},
		props:{
			chinaData:Object,
			worldData:Array,
			required:true,
		},
		methods:{
			onChange(index){
				//console.log(index)
				this.$emit('innerIndexW',index)
			},
			//
			getNowData(){
				let nowData=[];
				let totalData=[];
				let defineAreaName=[];//
				//console.log(this.areaData)
				let _this = this;
				nowData.push({name:'China',value:_this.chinaData.currentConfirmedCount})
				totalData.push({name:'China',value:_this.chinaData.confirmedCount})
				this.worldData.forEach(item=>{
					if(item.countryName==""){
						nowData.push({
							name:'United States',
							value:item.currentConfirmedCount
						})
						defineAreaName.push({
							EnglishName:item.countryEnglishName,
							ChineseName:item.countryName
						})
						totalData.push({
							name:'United States',
							value:item.confirmedCount
						})
					}
					else{
					nowData.push({
						name:item.countryEnglishName,
						value:item.currentConfirmedCount
					})
					totalData.push({
						name:item.countryEnglishName,
						value:item.confirmedCount
					})
					defineAreaName.push({
						EnglishName:item.countryEnglishName,
						ChineseName:item.countryName
					})
					}
				})
				//console.log(_this.chinaData.currentConfirmedCount)
				
				this.nowAreaData = nowData;
				this.totalAreaData = totalData;
				this.defineName=defineAreaName
			},
			//
			getChinaMap(data,ele,kind){//
				let myChart = this.$echarts.init(ele);
				window.onresize = myChart.resize;
				myChart.setOption({
					backgroundColor:'#F7F7F7',//
					title:{
						/* text:'China',
						subTitle:'china',
						color:'#000', */
						x:'center'
					},
					 //
					visualMap: {
						geo:{
							label:false
						},
						pieces:[
							{value:0,color:'#fff'},
							{gte:1,lte:9,color:'#FAEBD2'},
							{gte:10,lte:99,color:'#E9A188'},
							{gte:100,lte:499,color:'#D56355'},
							{gte:500,lte:999,color:'#BB3937'},
							{gte:1000,lte:10000,color:'#772526'},
							{gt:10000,color:'#480F10'}
						],
						orient:'horizontal',//
						calculable: false, //
						inverse:false,
						textGap:5,
						hoverLink:false,
						itemGap:5,
						itemWidth:10,//
						itemHeight:10,
						left:'center',
						right:'center',
						//show:false,//
						textStyle: {
							color: '#000',
							fontSize:'8',
							fontFamily:'Microsoft YaHei',
							lineHeight:'50',
							textAlign:'center'
						},
					},
					 // 
					tooltip:{
						triggerOn: 'click',
						enterable:false,//
						show:true, //
						trigger: 'item', //
						formatter:function(params,ticket,callback){
							//console.log(params)
							var html="";
							if(params.data==undefined){
							}
							else{
								return html =`
										<div class="tooltip">
											<div class="tooltipLeft">
												<div>${params.name}</div>
												<p>${kind}:${params.value}</p>
											</div>
										</div>			
									  `
							}
						},
						textStyle:{
							lineHeight:14,
						}
					},
					series:[
						{
							name:'WorldMap',
							roam:true,//
							type: 'map',  //
							map: 'world', //
							//nameMap:{'China':''},
							showLegendSymbol: true, // legend
							data:data,
							itemStyle: { // 
								fontSize:8,
								emphasis:{ //
									label:{
										show:true,
									}
								}
							},
							zoom:1.2,//
							label:false
						},
						{
							type:'scatter',
							showEffectOn: 'render',//
							coordinateSystem:'geo',//
							symbolSize:10,//
							data:[
								{name: '', value: [111.3,30.7,130]},
							],
							zlevel:99999
						},
					],
				}),
				window.addEventListener('resize', () => {
					// echarts
					myChart.resize();
				})
			}
		},
		created(){
			this.getNowData()
		},
		mounted(){
			
			this.getChinaMap(this.nowAreaData,this.$refs.wordlMap,"");
			this.getChinaMap(this.totalAreaData,this.$refs.worldMap2,"")
		}
	}
</script>

<style scoped="scoped">
	.chinaMapWrapper{
		width: 100%;
		height: 8.43rem;
	}
	.van-swipe{
		width: 100%;
		height: 100%;
	}
	.van-swipe-item{
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.chart1{
		width: 100%;
		min-width: 10rem;
		height: 8.43rem;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.tooltip{
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		font-size: 0.22rem;
		line-height: 14px;
	}

</style>
